
export default {
  data() {
    return {
      status: true,
      loading: false, // é®ç½©
      type: this.$route.query.type, // è¡¨å•æ“ä½œç±»å‹  save æ–°å¢ update ä¿®æ”¹
      id: this.$route.query.id, // ä»è·¯ç”±ä¼ é€’è¿‡æ¥çš„idï¼Œä»…é™åå°æ¥æ”¶å­—æ®µä¸ºidä½¿ç”¨ï¼Œè¯·çµæ´»ä½¿ç”¨
      errorMessage: {}, // åŠ¨æ€é”™è¯¯ä¿¡æ¯å¯¹è±¡
      timer: null // æµ‹è¯•ç”¨å®šæ—¶å™¨

    }
  },
  methods: {
    destroyed() { // æ‰‹åŠ¨é‡è½½
      this.status = false
      setTimeout(() => {
        this.status = true
      }, 1)
    },
    validateForm() {
      // åˆ¤æ–­æ˜¯å¦å…¨éƒ¨éªŒè¯é€šè¿‡
      const state = false

      return state
    },
    /** å¼‚æ­¥å›è°ƒï¼Œæ”¯æŒæˆåŠŸåç»§ç»­æ“ä½œ
     * æ™®é€šè¡¨å•çš„æäº¤å…¬ç”¨æ–¹æ³•
     * @param {Function} dataMethod æäº¤çš„æ–¹æ³•
     * @param {Object} data   æäº¤çš„å‚æ•°
     * @param {Boolean} state  æ˜¯å¦æœ‰è¿”å›é¡µ
     * @param {String} path   è¿”å›çš„è·¯å¾„
     * @param {String} type    è¿”å›çš„ç±»å‹
     * @param {Object} params  è¿”å›çš„å‚æ•°
     */
    saveForm(dataMethod, data, state, path, type, params = {}) {
      return new Promise(resolve => {
        this.$refs.form.validate((valid, item) => {
          if (valid) {
            this.loading = true
            dataMethod(data).then(res => {
              this.loading = false
              if (res.code === 200) {
                this.$successMessage('æ·»åŠ æˆåŠŸ')
                if (state) {
                  setTimeout(() => {
                    this.rest()
                    this.$routerLink(path, type, params)
                  }, 400)
                } else {
                  this.rest()
                }
                resolve(res)
              } else if (res.code === 100) { // æ‹¦æˆªå…·ä½“çš„é”™è¯¯ï¼Œé˜²æ­¢å‰å°é—æ¼
                let name = ''
                for (const key in res.data) {
                  name = key
                  break
                }
                this.errorPosition(name, res.data)
              } else {
                this.$errorMessage(res.code + ':' + res.message)
                this.loading = false
              }
            })
          } else { // å®šä½åˆ°é”™è¯¯çš„é‚£ä¸€è¡Œ
            this.loading = false
            let name = ''
            for (const key in item) {
              name = key
              break
            }
            this.errorPosition(name)
          }
        })
      })
    },
    /**
     * æ™®é€šè¡¨å•çš„ä¿®æ”¹ç¼–è¾‘å…¬ç”¨æ–¹æ³•
     * @param {Function} dataMethod æäº¤çš„æ–¹æ³•
     * @param {Object} data   æäº¤çš„å‚æ•°
     * @param {String} path   è¿”å›çš„è·¯å¾„
     * @param {String} type    è¿”å›çš„ç±»å‹
     * @param {Object} params  è¿”å›çš„å‚æ•°
     */
    updateForm(dataMethod, data, path, type, params = {}, state = true) {
      this.$refs.form.validate((valid, item) => {
        if (valid) {
          this.loading = true
          dataMethod(data).then(res => {
            this.loading = false
            if (res.code === 200) {
              this.$successMessage('ä¿®æ”¹æˆåŠŸ')
              if (state) {
                setTimeout(() => {
                  this.rest()
                  this.$routerLink(path, type, params)
                }, 700)
              }
            } else if (res.code === 100) { // æ‹¦æˆªå…·ä½“çš„é”™è¯¯ï¼Œé˜²æ­¢å‰å°é—æ¼
              this.loading = false
              this.errorMessage = {}
              setTimeout(() => {
                for (const key in res.data) {
                  this.$set(this.errorMessage, key, res.data[key])
                }
              }, 5)
            } else {
              this.loading = false
              this.$errorMessage(res.code + ':' + res.message)
            }
          })
        } else { // å®šä½åˆ°é”™è¯¯çš„é‚£ä¸€è¡Œ
          this.loading = false
          let name = ''
          for (const key in item) {
            name = key
            break
          }
          this.errorPosition(name)
        }
      })
    },
    errorPosition(name, errorInfo) {
      // é”™è¯¯ä¿¡æ¯å®šä½
      const lay = document.querySelector('#layout')
      if (this.$refs[name]) {
        const ele = this.$refs[name].$el
        lay.scrollTop = ele.offsetTop - 350
      }
      this.$refs['form'].validateField(name)
      this.validateError(errorInfo)
      return false
    },
    autoFocus() {
      this.$refs.form.clearValidate()
      // this.$refs.first.focus()
    },
    rest() {
      window.clearInterval(this.timer)
      this.autoFocus()
      this.$refs.form.resetFields()
      this.destroyed()
      // this.formData = Object.assign({}, defaultData)
    },
    validateError(data) { // é”™è¯¯ä¿¡æ¯å¯¹è±¡
      this.errorMessage = {}
      setTimeout(() => {
        for (const key in data) {
          this.$set(this.errorMessage, key, data[key])
        }
      }, 1)
    }
  }
}



