
<!--å›¾ç‰‡ç©ºé—´é€‰æ‹©-->
<template>
  <div>
    <div  @click="initData">
      <slot>
        <el-button type="primary" size="mini">ç´ æåº“é€‰æ‹©</el-button>
      </slot>
    </div>
    <el-dialog :visible="visible" :before-close="handleClose" title="å›¾æ–‡é€‰æ‹©" :append-to-body='true'>
      <div style="max-height:500px;overflow:auto;">
      <div class="we-card__list we-card__list--dialog" v-loading='loading' >
      <div class="we-card__warpper" v-for="(content) in tableData" :key="content.id"
       @click="setCheckedItems(content)"
      >
        <template v-for="(item,index) in content.requestArray">
          <div class="we-card__item we-card__item--thumb" v-if="index==0" :key="index">
            <div  class="we-card__link">
              <img class="we-card__img" :src="$cms.weChatUrl(item.thumbMediaUrl)" alt="">
              <span class="we-card__title--thumb" v-if="item.title!=''">
                {{item.title}}
              </span>
            </div>
          </div>
          <div class="we-card__item  we-card__item--sub" v-else :key="index">
            <div  class="we-card__link">
              <div class="we-card__sub--inner">
                <div class="we-card__sub--img">
                  <img :src="$cms.weChatUrl(item.thumbMediaUrl)" alt="">
                </div>
                <div class="we-card__sub--title">
                  {{item.title}}
                </div>
              </div>
            </div>
          </div>
        </template>
        <div class="we-card__footer">
          <span class="fl">
            æ›´æ–°æ—¶é—´ {{$cms.getDateDiff(content.wechatUpdateTime*1000)}}
          </span>
        </div>
        <!-- é€‰ä¸­é®ç½© -->
         <div class="we-card-checked" v-if="isChecked(content)">
           <i class="el-icon-check"></i>
         </div>
      </div>
    </div>
     </div>
      <cms-pagination @change="getPage"  :total='total' ></cms-pagination>
      
       <div class="el-dialog__footer">
          <el-button @click="setChange" type="primary" size="mini"  :disabled='checkedItems.length>0 ?false:true' >ç¡®å®š</el-button>
          <el-button @click="handleClose" type='info' size="mini" >å–æ¶ˆ</el-button>
        </div>
    </el-dialog>
  </div>
</template>
<script>
import {
  wechatMaterialPage
} from '@/api/wechat/index.js'

export default {
  name: 'imageTextZone',
  props: {
    size: [Number], // æ§åˆ¶é€‰ä¸­çš„æ•°é‡
    index: 0
  },
  data() {
    return {
      params: {
        name: '',
        type: 'news',
        page: 1,
        size: 10
      },
      total: 0, // å›¾ç‰‡æ€»æ•°
      baseUrl: process.env.baseUrl, // ç›¸å¯¹è·¯å¾„æ‹¼æ¥
      tableData: [],
      disable: true,
      checkAll: false,
      checkedItems: [], // é€‰ä¸­å›¾ç‰‡çš„æ•°ç»„
      selectedItem: {}, // å½“å‰æ“ä½œçš„å›¾ç‰‡å¯¹è±¡
      selectedIndex: [], // å½“å‰é€‰ä¸­çš„æ˜¯ç¬¬å‡ é¡¹
      visible: false,
      loading: false
    }
  },
  methods: {
    initData() {
      this.getTableData()
      this.visible = true
    },
    handleClose() {
      this.checkedItems = []
      this.visible = false
    },
    getTableData(params) {
      // è·å–åˆ°ç‚¹å‡»çš„å›¾ç‰‡ä¿¡æ¯
      // this.tableData = []
      wechatMaterialPage(this.params).then(res => {
        if (res.code === 200) {
          this.checkedItems = [] // é‡ç½®å…¨é€‰æ•°æ®
          this.total = res.data.totalElements
          this.tableData = res.data.content
        }
      })
    },
    isChecked(item) {
      // åˆ¤æ–­æ˜¯å¦é€‰ä¸­
      return this.checkedItems.findIndex(v => v.id === item.id) > -1
    },
    setCheckedItems(item, index) {
      this.checkedItems = [item]
    },
    setChange() {
      // æŠŠé€‰ä¸­çš„å¯¹è±¡ä¼ é€’å‡ºæ¥
      this.$emit('change', this.checkedItems, this.index)

      this.checkedItems = []
      this.selectedItem = {} // å½“å‰æ“ä½œçš„å›¾ç‰‡å¯¹è±¡
      this.selectedIndex = [] // å½“å‰é€‰ä¸­çš„æ˜¯ç¬¬å‡ é¡¹
      this.visible = false
    },
    getPage(current, size) {
      // åˆ†é¡µè°ƒç”¨
      this.params.page = current
      this.getTableData()
    }
  }
}
</script>





