import echarts from 'echarts'
import countTo from 'vue-count-to'
const moment = require('moment')
export default {
  components: {
    countTo
  },
  props: {
    timeData: {
      type: Array
    }
  },
  data() {
    return {
      chart: null, // å›¾è¡¨å®ä¾‹
      el: null, // æŒ‚è½½çš„å›¾æ ‡æ ‡è¯†
      optionsData: {},
      loading: false,
      queryParams: {
        startTime: moment().subtract(7, 'days').format('YYYY-MM-DD'),
        endTime: moment().subtract(1, 'days').format('YYYY-MM-DD')
      }
    }
  },
  watch: {
    timeData: {
      handler() {
        this.queryParams.startTime = this.timeData[0]
        this.queryParams.endTime = this.timeData[1]
        this.getData()
      },
      deep: true
    }

  },
  methods: {
    getData() { }, // åªè¦å¤å†™è¿™ä¸ªå‡½æ•°å°±å¯ä»¥æ“ä½œå¯¹åº”çš„å›¾è¡¨æ•°æ®,
    initChart() { // åˆå§‹åŒ–å›¾è¡¨
      this.$nextTick(() => {
        if (this.el && this.el !== '') {
          this.chart = echarts.init(document.getElementById(this.el), 'walden')
        }
      })
    }
  },
  created() {
    this.getData()
  },
  mounted() { // ç›‘å¬å›¾è¡¨
    this.initChart()
    window.addEventListener('resize', () => {
      if (this.chart) {
        this.chart.resize()
      }
    })
  }

}



