<template>
  <div>
    <ul id="treeDemo" class="ztree" ref="treeDemo"></ul>
  </div>
</template>
<script>
import { menuManagerFindAll } from '@/api/admin/menuManager.js'
export default {
  name: 'roleTree',
  props: {
    menuIds: {// èœå•çš„idé›†åˆ
      type: Array,
      default: function() {
        return []
      }
    },
    type: {
      type: Number,
      default: 1
    }
  },
  data() {
    return {
      treeObj: null,
      zNodes: [],
      clickFlag: null, // æ˜¯å¦ç‚¹å‡»æ ‡è¯†ï¼ˆå®šæ—¶å™¨ç¼–å·ï¼‰
      setting: {
        callback: {
          onClick: this.setPermsInfo,
          onDblClick: this.setExpand
        },
        check: {
          enable: true // å¼€å¯å‹¾é€‰
        },
        data: {
          key: {
            children: 'childs'
          }
        },
        view: {
          dblClickExpand: false,
          expandSpeed: ''
        }
      }
    }
  },
  methods: {
    getRoleTree() {
      // åˆå§‹åŒ–æƒé™æ ‘
      menuManagerFindAll({ platformType: this.type }).then(res => {
        this.zNodes = res.data
        this.$nextTick(function() {
          this.treeObj = $.fn.zTree.init(
            $('#treeDemo'),
            this.setting,
            this.zNodes
          )
          this.defaultCheckNode(this.menuIds)
        })
      })
    },
    defaultCheckNode(arr) {
      if (arr.length === 0) {
        return false
      } else {
        if (this.treeObj) {
          this.treeObj.checkAllNodes(false)
          this.treeObj.expandAll(false)
          for (let i = 0; i < arr.length; i++) {
            const node = this.treeObj.getNodeByParam('id', arr[i])
            this.treeObj.checkNode(node, true, false)
            // this.treeObj.expandNode(node)
          }
        }
      }
    },
    setPermsInfo(e, treeId, treeNode, clickFlag) {
      // å•å‡»æ§åˆ¶é€‰æ‹©
      if (this.clickFlag) {
        // å–æ¶ˆä¸Šæ¬¡å»¶æ—¶æœªæ‰§è¡Œçš„æ–¹æ³•
        this.clickFlag = clearTimeout(this.clickFlag)
      }
      this.clickFlag = setTimeout(() => {
        this.treeObj.checkNode(treeNode, !treeNode.checked, true)
      }, 200) // å»¶æ—¶300æ¯«ç§’æ‰§è¡Œ
    },
    getPermsInfo() {
      // è·å–åˆ°å½“å‰é€‰ä¸­çš„id
      return this.treeObj.getCheckedNodes()// è¿”å›é€‰ä¸­çš„èœå•å¯¹è±¡
    },
    setExpand(event, treeId, treeNode) {
      if (this.clickFlag) {
        // å–æ¶ˆä¸Šæ¬¡å»¶æ—¶æœªæ‰§è¡Œçš„æ–¹æ³•
        this.clickFlag = clearTimeout(this.clickFlag)
      }
      this.treeObj.expandNode(treeNode)
    }
  },
  created() {
    this.getRoleTree()
  },
  watch: {
    menuIds: {
      handler() {
        this.defaultCheckNode(this.menuIds)
      },
      deep: true
    }
  },
  mounted() {}
}
</script>

<style>
</style>



