        <el-form :model="formData" status-icon ref="form" label-width="150px" class="demo-ruleForm">
          <el-form-item label="éŸ³ä¹æ ‡é¢˜">
            <el-input type="text" maxlength="150" v-model="formData.title" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="éŸ³ä¹é“¾æ¥">
            <el-input  type="text" maxlength="255" v-model="formData.musicUrl" autocomplete="off">
              <cms-picture-zone  slot="append" type='music' viewType='list' @change="getAudio">
                <el-button style="position:relative;top:5px;" size='small'>é€‰æ‹©éŸ³ä¹æ–‡ä»¶</el-button>
              </cms-picture-zone>
            </el-input>
            <div class="gray">é€‰æ‹©ä¸Šä¼ çš„éŸ³é¢‘æ–‡ä»¶æˆ–ç›´æ¥è¾“å…¥URLåœ°å€ï¼Œå¸¸ç”¨æ ¼å¼ï¼šmp3</div>
          </el-form-item>
          <el-form-item label="é«˜å“è´¨éŸ³ä¹é“¾æ¥">
            <el-input maxlength="255" v-model.number="formData.hqMusicUrll"></el-input>
            <div class="gray">é«˜è´¨é‡éŸ³ä¹é“¾æ¥ï¼ŒWIFIç¯å¢ƒä¼˜å…ˆä½¿ç”¨è¯¥é“¾æ¥æ’­æ”¾éŸ³ä¹</div>
          </el-form-item>
          <el-form-item label="æè¿°">
            <el-input maxlength="30" v-model.number="formData.description"></el-input>
            <div class="gray">è¾“å…¥ç®€çŸ­çš„éŸ³ä¹æè¿°ï¼Œ30å­—ä»¥å†…</div>
          </el-form-item>
          <el-form-item label="å›¾ç‰‡" :required="true">
            <image-zone class="wx-create__item" @change="getImages">
              <div class="wx-create__item">
                <p class="t-icon"><img src="./../../../../../assets/images/icon-img.png" alt=""></p>
