<script>
//  wxTextZone
import form from '@/mixins/form'
import imageZone from './wxPictureZone'
export default {
  mixins: [form],
  name: 'wxMusicZone',
  components: {
    imageZone
  },
  props: {
    value: {},
    multiple: {
      type: Boolean,
      default: false
    },
    labelName: {
      type: String,
      default: 'name'
    }
  },
  data() {
    return {
      dialogVisible: false,
      formData: {
        hqMusicUrll: '',
        musicUrl: '',
        title: '',
        description: '',
        msgType: 'music',
        thumbMediaId: ''
      },
      params: null
    }
  },
  methods: {
    getImages(items) { // è·å–å›¾ç‰‡ä¿¡æ¯
      this.params = {
        url: this.$cms.weChatUrl(items[0].request.url),
        name: items[0].request.title
      }
      this.formData.thumbMediaId = items[0].id
    },
    getAudio(item, index) {
      if (item[0].url.indexOf('h') === 0) {
        this.formData.musicUrl = item[0].url
      } else {
        this.formData.musicUrl = process.env.baseUrl + item[0].url
      }
    },
    initData() {
      this.dialogVisible = true
      // this.getTableData(wechatMaterialPage, this.queryParams)
    },
    determine() {
      this.$emit('change', this.formData)
      this.close()
    },
    close(done) {
      this.formData = {
        hqMusicUrll: '',
        musicUrl: '',
        title: '',
        description: '',
        msgType: 'music',
        thumbMediaId: ''
      }
      this.dialogVisible = false
    }
  }
}
</script>

<style lang='scss' scoped>
.t-icon{
