<template>
  <div>
  </div>
</template>
<script>
import { refundCheck } from '@/api/order/index.js'
export default {
  data() {
    return {
      visible: false,
      addressVisable: false, // ç¡®è®¤æŒ‰é’®å¯¹åº”å¼¹å‡ºæ¡†çš„æ˜¾ç¤ºéšè—
      addressLoading: false,
      serviceParams: {
        id: '',
        checked: false,
        checkReason: '',
        addressId: ''
      }
    }
  },
  methods: {
    checkService(data, status) { // å“åº”å¯¹åº”çš„æ“ä½œ
      // type å”®åç±»å‹ id å”®åid status æ˜¯å¦ç¡®è®¤ refundStatus  å”®åçŠ¶æ€
      const type = parseInt(data.orderRefund.refundType)
      const id = data.orderRefund.id
      if (status) { // åŒæ„å”®å
        if (type === 1) { // ä»…é€€æ¬¾
          this.refundPrice(id)
        } else { // é€€æ¬¾é€€è´§å’Œæ¢è´§çš„ç¡®è®¤æ“ä½œ
          this.refundConfirm(id)
        }
      } else {
        this.refusedService(id)// ä¸é€šè¿‡
      }
    },

    refundPrice(id) { // ä»…é€€æ¬¾æ“ä½œ
      this.$confirm(
        'ç¡®è®¤é€šè¿‡åï¼Œç”³è¯·å°†ç›´æ¥åˆ¤å®šä¸ºå·²å®Œæˆçš„çŠ¶æ€ï¼Œä¸ºé¿å…å–å®¶ä¸å¹³å°çš„äº‰è®®ï¼Œå»ºè®®ä¸ä¹°å®¶åŠå–å®¶åå•†ã€‚è´§æ¬¾å°†é€€è‡³ä¹°å®¶åŸæ”¯ä»˜è´¦æˆ·ä¸­ï¼Œåç»­æ— æ³•æ’¤å›ï¼Œç¡®è®¤æ˜¯å¦ç»§ç»­ï¼Ÿ',
        'ç³»ç»Ÿæç¤º',
        {
          confirmButtonText: 'ç¡®å®š',
          cancelButtonText: 'å–æ¶ˆ',
          type: 'warning'
        }
      )
        .then(res => {
          const params = {
            id: id,
            checked: true
          }
          this.refundCheck(params)
        })
        .catch(res => {
          return false
        })
    },
    refusedService(id) { // æ‹’ç»å”®åç”³è¯·
      this.$prompt(
        'å¦‚ç¡®è®¤ä¸é€šè¿‡ä¹°å®¶çš„å”®åç”³è¯·ï¼Œç”³è¯·å°†ç›´æ¥åˆ¤å®šä¸ºä¸é€šè¿‡ï¼Œä¹°å®¶æ— æ³•å†è¿›è¡Œè¯¥å”®åç”³è¯·å•æ“ä½œï¼Œå»ºè®®ä¸ä¹°å®¶åŠå–å®¶åå•†ã€‚æ˜¯å¦ç»§ç»­ï¼Ÿ',
        'ç³»ç»Ÿæç¤º',
        {
          confirmButtonText: 'ç¡®å®š',
          cancelButtonText: 'å–æ¶ˆ',
          inputValidator: value => {
            if (value && value !== '') {
              return !(value.length > 140)
            } else {
              return true
            }
          },
          inputType: 'textarea',
          inputErrorMessage: 'è¯´æ˜è¿‡é•¿',
          inputPlaceholder: 'è¯·è¾“å…¥è¯´æ˜åŸå› '
        }
      )
        .then(({ value }) => {
          this.serviceParams.id = id
          this.serviceParams.checked = false
          this.serviceParams.checkReason = value
          this.refundCheck(this.serviceParams)
        })
        .catch(() => {})
    },
    refundConfirm(id) { // é€€æ¬¾é€€è´§å’Œæ¢è´§çš„ç¡®è®¤æ“ä½œ
      this.$confirm(
        'ç¡®è®¤é€šè¿‡åï¼Œç”³è¯·å°†ç›´æ¥åˆ¤å®šä¸ºå¾…ä¹°å®¶å‘è´§çŠ¶æ€ï¼Œå–å®¶æ”¶è´§åœ°å€é‡‡ç”¨å–å®¶é»˜è®¤åœ°å€ï¼Œä¸ºé¿å…å–å®¶ä¸å¹³å°çš„äº‰è®®ï¼Œå»ºè®®ä¸ä¹°å®¶åŠå–å®¶åå•†ã€‚è´§æ¬¾å°†é€€è‡³ä¹°å®¶åŸæ”¯ä»˜è´¦æˆ·ä¸­ï¼Œåç»­æ— æ³•æ’¤å›ï¼Œç¡®è®¤æ˜¯å¦ç»§ç»­ï¼Ÿ',
        'ç³»ç»Ÿæç¤º',
        {
          confirmButtonText: 'ç¡®å®š',
          cancelButtonText: 'å–æ¶ˆ',
          type: 'warning'
        }
      )
        .then(res => {
          const params = {
            id: id,
            checked: true
          }
          this.refundCheck(params)
        })
        .catch(res => {
          return false
        })
    },
    refundCheck(params) { // å‘é€å®¡æ ¸
      refundCheck(params).then(res => {
        this.addressLoading = true
        if (res.code === 200) {
          this.$successMessage('æ“ä½œæˆåŠŸ')
          this.$emit('change', true)
        } else {
          this.$emit('change', false)
          this.$errorMessage(res.message)
        }
        this.addressLoading = false
        this.addressVisable = false
      })
    },
    handleClose() {
      this.addressVisable = false
      this.visible = false
    }
  }
}
</script>

 <style lang="scss" scoped>
.addr-items{
  width: 100%;
 padding: 10px 15px;
  border-bottom: 1px solid #e0e0e0;
}


</style>



