          <div class="text-overflow">
            <span>è§„åˆ™åï¼š</span>{{item.ruleName}}</div>
          <span>å…³é”®è¯ï¼š</span>
          <template v-for="(wordItem,index2) in item.wechatReplyKeywordList">
            <el-tag size="mini" v-if="wordItem.wordkeyEq!==''" :key="index2+'wordItem.wordkeyEq'" style="margin-left:5px;">{{wordItem.wordkeyEq}}</el-tag>
            <el-tag size="mini" v-if="wordItem.wordkeyLike!==''" :key="index2+'wordItem.wordkeyLike'" style="margin-left:5px;">{{wordItem.wordkeyLike}}</el-tag>
          </template>
          <div>
            <span>å›å¤ç±»å‹ï¼š</span>
            <span v-if="item.msgType ==='image'">å›¾ç‰‡</span>
            <span v-if="item.msgType ==='text'">æ–‡æœ¬</span>
            <span v-if="item.msgType ==='news'">å›¾æ–‡</span>
            <span v-if="item.msgType ==='voice'">éŸ³ä¹</span>
            <span v-if="item.msgType ==='video'">è§†é¢‘</span>
            <span v-if="item.msgType ==='music'">éŸ³ä¹</span>
          </div>
        </div>
