        <el-button type='info' size="mini" @click="handleClose">å– æ¶ˆ</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import list from '@/mixins/list'
import { keywordPage } from '@/api/wechat/autoResponse.js'
export default {
  name: 'wxPictureZone',
  props: {
    size: [Number], // æ§åˆ¶é€‰ä¸­çš„æ•°é‡
    index: 0
  },
  mixins: [list],
  data() {
    return {
      queryParams: {
        page: 1,
        size: 10
      },
      tableData: [],
      checkedItems: [], // é€‰ä¸­å›¾ç‰‡çš„æ•°ç»„
      visible: false
    }
  },
  methods: {
    initData() {
      this.visible = true
      this.getPictureItems()
    },
    handleClose() {
      this.checkedItems = []
      this.visible = false
    },
    getPictureItems() {
      this.getTableData(keywordPage, this.queryParams)
      // keywordPage(this.queryParams).then(res => {
      //   if (res.code === 200) {
      //     this.checkedItems = [] // é‡ç½®å…¨é€‰æ•°æ®
      //     this.total = res.data.totalElements
      //     this.tableData = res.data.content
      //   }
      // })
    },
    isChecked(item) {
      // åˆ¤æ–­æ˜¯å¦é€‰ä¸­
      return this.checkedItems.findIndex(v => v.id === item.id) > -1
    },
    setCheckedItems(item, index) {
      this.checkedItems = [item]
    },
    setChange() {
      // æŠŠé€‰ä¸­çš„å¯¹è±¡ä¼ é€’å‡ºæ¥
      this.$emit('change', this.checkedItems, this.index)
      this.checkedItems = []
      this.visible = false
    }
  }
}
</script>

<style scoped lang='scss'>
.keyword-items--active {
  border: 1px solid #5cb85c;
  position: relative;
  &::before {
    content: "";
    width: 20px;
    height: 20px;
    background: url("./../../../../../assets/images/img-check.png");
    background-size: contain;
    position: absolute;
    top: 10px;
    right: 10px;
  }
}
</style>




