
<!--å›¾ç‰‡ç©ºé—´é€‰æ‹©-->
<template>
  <div>
    <div  @click="visible=true">
      <slot>
        <el-button type="primary" size="mini">ç´ æåº“é€‰æ‹©</el-button>
      </slot>
    </div>
    <el-dialog width="870px"  :visible="visible" :before-close="handleClose" title="å›¾ç‰‡é€‰æ‹©" :append-to-body='true'>
      <div class="cms-picture-zone" style="margin-left:0">
        <div class="cms-picture-zone__inner">
          <div class="cms-picture-zone__header">
            <span class="fl">å›¾ç‰‡é€‰æ‹©</span>
            <div class="fr cms-picture-zone__search">
              <!-- è¿™é‡Œä¸€å®šè¦æ”¾åœ¨åé¢ï¼Œä¸ç„¶çš„è¯ æŒ‰å›è½¦ä¼šè§¦å‘ä¸Šä¼ -->
              <cms-upload :on-change='getUpload' class="upload-abs">
                <el-button icon='up' type="primary" size='mini' >ä¸Šä¼ </el-button>
              </cms-upload>
            </div>
          </div>
          <!--å›¾ç‰‡åˆ—è¡¨-->
          <div class="cms-picture-zone__body" ref="imgWarpper" id="imgWarpper">
            <ul class="zone--items--flex" ref="picItems" v-if="pictureItems.length>0">
              <li class="zone--items" :class="[isChecked(item) ? 'checked' : '']" v-for="(item,index) in pictureItems" :key="index" @click="setCheckedItems(item,index)">
                <div class="body--img-box">
                  <img :src="
              item.request.isLocal ? item.request.url : $cms.weChatUrl(item.request.url)" alt="item.request.name" :title="item.request.name">
                </div>
                <div class="body--img-name">
                  <span>{{item.request.name}}</span>
                </div>
                <div class="upload-cover" v-if="item.request.isAdd">
                  <el-progress type="circle" :percentage="item.request.percent" class="cover-progress" :status="item.request.status?'success':'exception'"></el-progress>
                </div>
              </li>
            </ul>
            <!-- æ— å†…å®¹æ˜¾ç¤º -->
            <div class="no-pictuer" v-else>
              <div>æš‚æ— å†…å®¹ï¼Œç‚¹å‡»
                <cms-upload :on-change='getUpload'>
                  <span class="upload-font">ä¸Šä¼ </span>
                </cms-upload>æ·»åŠ èµ„æº</div>
            </div>
          </div>
        </div>
        <div class="el-dialog__footer footer-flex">
          <cms-pagination @change="getPage" :size='15' :total='total' align='left' layout='prev, pager, next' class="zone-pagination"></cms-pagination>
           <el-button @click="setChange" type='primary' size="mini" :disabled='checkedItems.length>0 ?false:true' class="abs-button">ç¡®å®š</el-button>
        </div>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import {
  wechatMaterialPage,
  materialUpload
} from '@/api/wechat/index.js'
export default {
  name: 'wxPictureZone',
  props: {
    size: [Number], // æ§åˆ¶é€‰ä¸­çš„æ•°é‡
    index: 0
  },
  data() {
    return {
      params: {
        name: '',
        type: 'image',
        page: 1,
        size: 10
      },
      total: 0, // å›¾ç‰‡æ€»æ•°
      baseUrl: process.env.baseUrl, // ç›¸å¯¹è·¯å¾„æ‹¼æ¥
      pictureItems: [],
      disable: true,
      checkAll: false,
      checkedItems: [], // é€‰ä¸­å›¾ç‰‡çš„æ•°ç»„
      selectedItem: {}, // å½“å‰æ“ä½œçš„å›¾ç‰‡å¯¹è±¡
      selectedIndex: [], // å½“å‰é€‰ä¸­çš„æ˜¯ç¬¬å‡ é¡¹
      visible: false
    }
  },
  methods: {
    handleClose() {
      this.checkedItems = []
      this.visible = false
    },
    getUpload(fileList) {
      // è·å–åˆ°ä¸Šä¼ çš„æ–‡ä»¶
      this.$nextTick(() => {
        this.$refs.imgWarpper.scrollTop = 0
      }) // æ»šåŠ¨çš„æ—¶å€™å›åˆ°æœ€ä¸Šé¢å»
      for (const key in fileList) {
        if (fileList.hasOwnProperty(key)) {
          const obj = {
            request: {
              url: window.URL.createObjectURL(fileList[key]),
              name: fileList[key].name,
              isAdd: true,
              status: true,
              isLocal: true
            }
          }
          this.checkAll = false
          this.pictureItems.unshift(obj)
          this.uploadFile(fileList[key], key)
        }
      }
    },
    uploadFile(file, index) {
      // file ä¸Šä¼ çš„æ–‡ä»¶å¯¹è±¡ index æ‰¹é‡ä¸Šä¼ ç¡®å®šæ˜¯ç¬¬å‡ ä¸ª ä¸Šä¼ çš„è‡ªå®šä¹‰å®ç°
      const param = new FormData() // åˆ›å»ºformå¯¹è±¡
      const type = 'image'
      param.append('uploadFile', file, file.name) // é€šè¿‡appendå‘formå¯¹è±¡æ·»åŠ æ•°æ®
      param.append('type', type)
      materialUpload(
        param,
        res1 => {
          // å¤„ç†ä¸Šä¼ è¿›åº¦çš„æ˜¾ç¤º
          const loaded = res1.loaded
          const total = res1.total
          const percent = Math.round(loaded / total * 100)
          this.$set(this.pictureItems[index].request, 'percent', percent) // ç»™æ¯ä¸€ä¸ªä¸Šä¼ é¡¹èµ‹å€¼,è¿™é‡Œå°±ä¼šæœ‰ä¸Šä¼ çš„æ•ˆæœäº†
        },
        res2 => {
          if (res2.code === 200) {
            this.$set(
              this.pictureItems[index],
              'id',
              res2.data.id
            )
            this.$set(
              this.pictureItems[index],
              'request',
              res2.data.request
            )
            this.$set(this.pictureItems[index].request, 'status', true) // åˆ¤æ–­è¿›åº¦æˆåŠŸ
            setTimeout(() => {
              this.$set(this.pictureItems[index].request, 'isAdd', false) // æˆåŠŸå»æ‰é®ç½©
            }, 1000)
          } else {
            this.$set(this.pictureItems[index].request, 'status', false) // åˆ¤æ–­è¿›åº¦å¤±è´¥
            this.$errorMessage(res2.message)
            setTimeout(() => {
              this.pictureItems.splice(index, 1)
            }, 1000)
          }
        }
      )
    },
    getPictureItems(params) {
      // è·å–åˆ°ç‚¹å‡»çš„å›¾ç‰‡ä¿¡æ¯
      // this.pictureItems = []
      wechatMaterialPage(this.params).then(res => {
        if (res.code === 200) {
          this.checkedItems = [] // é‡ç½®å…¨é€‰æ•°æ®
          this.total = res.data.totalElements
          this.pictureItems = res.data.content
        }
      })
    },
    isChecked(item) {
      // åˆ¤æ–­æ˜¯å¦é€‰ä¸­
      return this.checkedItems.findIndex(v => v.id === item.id) > -1
    },
    setCheckedItems(item, index) {
      this.checkedItems = [item]
    },
    setChange() {
      // æŠŠé€‰ä¸­çš„å¯¹è±¡ä¼ é€’å‡ºæ¥
      this.$emit('change', this.checkedItems, this.index)
      this.checkedItems = []
      this.selectedItem = {} // å½“å‰æ“ä½œçš„å›¾ç‰‡å¯¹è±¡
      this.selectedIndex = [] // å½“å‰é€‰ä¸­çš„æ˜¯ç¬¬å‡ é¡¹
      this.visible = false
    },
    getPage(current, size) {
      // åˆ†é¡µè°ƒç”¨
      this.params.page = current
      this.getPictureItems()
    }
  },
  created() {
    this.getPictureItems()
  }
}
</script>





