const b = '<i class="emoji emoji%s"></i>'

export function encodeUnicode(str) {
  var res = []
  for (var i = 0; i < str.length; i++) {
    res[i] = ('00' + str.charCodeAt(i).toString(16)).slice(-4)
  }
  return '\\u' + res.join('\\u')
}
// è§£ç 
export function decodeUnicode(str) {
  str = str.replace(/\\/g, '%')
  return unescape(str)
}
export function emojiString(str) {
  for (var c in emojiList) {
    for (; str.indexOf(c) !== -1;) { str = str.replace(c, b.replace('%s', emojiList[c])) }
  }
  return str
}

