import { refundGet, refundFlowInfo } from '@/api/order/index.js'
export default{
  data() {
    return {
      tableData: '',
      loading: false,
      refundDetails: [],
      buyerFlowList: [], // ä¹°å®¶ç‰©æµä¿¡æ¯
      sellerFlowList: []// å–å®¶ç‰©æµä¿¡æ¯
    }
  },
  filters: {
    refundStatusFilter(status) {
      const typeArr = ['å¾…å–å®¶ç¡®è®¤', 'é€€æ¬¾ä¸­', 'å–å®¶æ‹’ç»', '', 'å¾…ä¹°å®¶å‘è´§', 'å¾…å–å®¶æ”¶è´§', 'å¾…ä¹°å®¶æ”¶è´§', 'å¾…å–å®¶å‘è´§', 'å·²å®Œæˆ', 'ä¹°å®¶å·²å–æ¶ˆ']
      return typeArr[status]
    },
    refundTypeFilter(type) { // æ”¶è´§æœåŠ¡ç±»å‹è¿‡æ»¤å™¨
      const typeArr = ['ä»…é€€æ¬¾', 'é€€è´§é€€æ¬¾', 'æ¢è´§']
      return typeArr[parseInt(type) - 1]
    },
    payModeFilter(type) { // è¡¨æ ¼ä¿¡æ¯æ–¹å¼
      const modeArr = ['æ”¯ä»˜å®', 'å¾®ä¿¡', 'é“¶è”', 'ä½™é¢']
      return modeArr[parseInt(type) - 1]
    },
    platformStatusFilter(status) { // å¹³å°ä»‹å…¥å¤„ç†çŠ¶æ€
      const statusArr = ['å¾…ç¡®å®š', 'ç¡®è®¤é€šè¿‡', 'ç¡®è®¤ä¸é€šè¿‡']
      return statusArr[parseInt(status)]
    }
  },
  methods: {
    getTableData(id) {
      this.loading = true
      refundGet({ id: id }).then(res => {
        if (res.code === 200) {
          this.tableData = res.data
          this.loading = false
          if (res.data.orderRefundDetails.length > 0) {
            var details = []
            res.data.orderRefundDetails.forEach(v => {
              details.push(v)
            })
            this.refundDetails = details.reverse()
          }
          if (res.data.orderRefund.status >= 5 && res.data.orderRefund.refundType > 1) {
            // å½“çŠ¶æ€ä¸ºå¾…å–å®¶æ”¶è´§ä¸”ç±»å‹ä¸ºé€€æ¬¾é€€è´§å’Œæ¢è´§æ—¶è·å–ç‰©æµä¿¡æ¯
            refundFlowInfo({ 'id': res.data.orderRefund.id }).then(flowInfo => { // è·å–ç‰©æµä¿¡æ¯
              if (flowInfo.code === 200) {
                flowInfo.data.forEach(v => { // 1.å–å®¶ 2.ä¹°å®¶
                  if (v.userType === 1) {
                    this.sellerFlowList = v.traces.reverse()
                  } else {
                    this.buyerFlowList = v.traces.reverse()
                  }
                })
              }
            })
          }
        } else {
          this.$errorMessage(res.message)
          this.loading = false
        }
      })
    },
    operateFilter(val) { // å”®åæµç¨‹
      var arr = ['æäº¤ç”³è¯·', 'å–å®¶åŒæ„', 'å–å®¶æ‹’ç»', 'ç”³è¯·æ’¤å›', 'å¹³å°ä»‹å…¥å¤„ç†ä¸­', 'ä¹°å®¶å·²å‘è´§', 'å–å®¶ç¡®è®¤æ”¶è´§', 'å–å®¶å·²å‘è´§', 'ä¹°å®¶ç¡®è®¤æ”¶è´§', 'ä¿®æ”¹å”®åç”³è¯·', '', '', 'å¹³å°å®¡æ ¸é€šè¿‡', 'å¹³å°å®¡æ ¸æ‹’ç»']
      return arr[parseInt(val)]
    },
    changeService() {
      this.getTableData(this.$route.query.id)
    },
    checkService(item, status) {
      // æ‰§è¡Œå”®åæ“ä½œ
      this.$refs.checkService.checkService(item, status)
    },
    showAttr(data) {
      // è½¬æ¢jsonå­—ç¬¦ä¸²å±æ€§
      if (data.length <= 0) {
        return ''
      } else {
        let str = ''
        data.forEach(v => {
          const tmp = v.attrName + ':' + v.attrValueName + '  '
          str = str + tmp
        })
        return str
      }
    }
  },
  created() {
    this.getTableData(this.$route.query.id)
  }
}



