export const materialUpload = (params, callback1, callback2) => {
  return axios({
    url: '/admin/platform/wechatMaterial/saveMaterial',
    headers: { 'Content-Type': 'multipart/form-data' },
    method: 'post',
    onUploadProgress: function(progressEvent) { // åŸç”Ÿè·å–ä¸Šä¼ è¿›åº¦çš„äº‹ä»¶
      if (progressEvent.lengthComputable) {
        // å±æ€§lengthComputableä¸»è¦è¡¨æ˜æ€»å…±éœ€è¦å®Œæˆçš„å·¥ä½œé‡å’Œå·²ç»å®Œæˆçš„å·¥ä½œæ˜¯å¦å¯ä»¥è¢«æµ‹é‡
        // å¦‚æœlengthComputableä¸ºfalseï¼Œå°±è·å–ä¸åˆ°progressEvent.totalå’ŒprogressEvent.loaded
        callback1(progressEvent)
      }
    },
    data: params
  }).then(res => {
    callback2(res)
  })
}

